export default {
	props: {
		title: {
			type: String,
			default: '操作提示'
		},
		text: {
			type: String,
			default: '提示内容'
		},
		noCancel: {
			type: Boolean,
			default: false
		},
		cancelText: {
			type: String,
			default: '取消'
		},
		confirmText: {
			type: String,
			default: '确定'
		},
		confirmColor: {
			type: String,
			default: 'rgb(0, 122, 255)'
		},
		hidden: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			hide: this.hidden,
			pageCloak: false,		// 小程序端初始化时，组件闪烁
			txt:false
		};
	},
	watch: {
		hidden(nVal, oVal){
			this.hide = nVal
		},
		hide(nVal, oVal){
			this.$emit('update:hidden', nVal)
		}
	},
	mounted() {
		this.pageCloak = true
	},
	methods: {
		empty(){},
		confirm(e){
			this.hide = false
			// console.log('confirm')
			this.$emit('confirm', e)
			
		},
		cancel(e){
			this.hide = false
			// console.log('cancel')
			this.$emit('cancel', e)
		}
	}
}
