		},
        methods: {
			...mapMutations(['GetUserInfo']),
			cancel(){
				this.confirmColor="hidden";	
				this.ismodals=false
				this.ismodal=true
			},
			cancelclose(){
				this.confirmYKColors="hidden";	
				this.ismodals=false
				this.ismodal=true
			},
			confirmButton: function() {
				console.log('Dialog 确定按钮点击');
				uni.navigateTo({
				    url: '../Login/Login',
					animationType: 'pop-in',
					animationDuration: 200
				})
				
			},
			cancelButton: function() {
				console.log('Dialog 取消按钮点击');
			},
			confirm(){
				this.confirmColor="hidden";	
				this.ismodals=false
				this.ismodal=true
				uni.navigateTo({
					url:'../../../pages/function/Buygasrolls/Buygasrolls',
					animationType: 'pop-in',
					animationDuration: 200
				})
			},
			ImgClick(text){
				var url
				if(text=='Jingpin'){
					url='../../function/Prerefueling/Prerefueling'
				}
				else{
					url='../../function/Unicom/Unicom'
				}
				uni.navigateTo({
					url:url,
					animationType: 'pop-in',
					animationDuration: 200
				})
			},
			confirmredit(){
				this.confirmYKColors="hidden";	
				this.ismodals=false
				this.ismodal=true
				uni.switchTab({
					url: '/pages/tabBar/gasCard/gasCard'
				});
			},
			confirms(){
				var cons=this.$refs.cons
				this.confirmColors="hidden"
				this.ismodals=false
				this.ismodal=true
			},
			
            triggerCollapses(e) {
				var urls=this.grids[e].url;
				var name=this.grids[e].text;
				console.log(urls)
				if(urls==''){
					console.log("电子券")
					if(name=="电子券"){
						this.confirmColor="visible"
						this.ismodals=true	
						this.ismodal=false
					}
					else if(name=="油卡充值"){
						this.confirmYKColors="visible"
						this.ismodals=true	
						this.ismodal=false
					}
					else{
						this.confirmColors="visible"
						this.ismodals=true
						this.ismodal=false
					}	
				}
				else{
					console.log("bbbbbbbb")
					uni.navigateTo({
					    url: urls,
						animationType: 'pop-in',
						animationDuration: 200
					})
				}
            },
			//获取值判断执行那个命令
			getfunction(inx){
				var urls=this.funlist[inx-1].url;
				if(inx==1){//扫码功能
					uni.scanCode({
						success: function (res) {
							console.log('条码类型：' + res.scanType);
							console.log('条码内容：' + res.result);
						}
					});
					return;
				}
				if(inx==4||inx==2||inx==3){
					if(this.UserInfo.userphone=='请登录'){
						this.$refs.simpleDialog2.confirm({
							title: '提示！',
							message:this.sDialog.message
						});
						return;
					}
				}
				uni.navigateTo({
				    url: urls,
					animationType: 'pop-in',
					animationDuration: 200
				})
				
			},
			gotos(){
				uni.navigateTo({
				    url: '../../Businessmen/Testing/Testing',
					animationType: 'pop-in',
					animationDuration: 200
				})
			},
            goDetailPage(e) {
                let url = ~e.indexOf('pages') ? e : '/pages/API/' + e + '/' + e;
                uni.navigateTo({
                    url: url,
					animationType: 'pop-in',
					animationDuration: 200
                })
            },
			shopshow(){
                this.show    = true;
                this.msgtype = "right";
                this.msg     = "操作成功 ^_^";
				this.flag    = true
                //延迟关闭
                var _self = this;
                if (this.alertTimer != null) { clearTimeout(this.alertTimer);}
                this.alertTimer = setTimeout(function(){
					_self.show = false;
					 _self.msg = '';
					 this.flag=false;
					 console.log(this.flag);
					 setTimeout(function(){
						 _self.show = '';
						  _self.msg = '';
						  this.flag=false;
					 },2000)
					 }, 2000);
				
            },
